@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Open Sans", sans-serif;
}

.App {
  text-align: center;
  background: #f6f6f6;
}

.scene {
  border: 1px solid #e0e0e0;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  box-sizing: border-box;
  height: 100vh;
}

.scene-wrapper {
  position: relative;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: calc(100% - 100px - 300px);
  height: calc(100vh - 61px);
  background: #e4e4e4;
  box-sizing: border-box;
}

.actions-wrapper {
  width: 100px;
  background: #f6f6f6;
  border-right: 2px solid #e0e0e0;
  border-bottom: 2px solid #e0e0e0;
  padding-top: 20px;
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
}

.topbar-wrapper {
  height: 60px;
  background: #f6f6f6;
  padding-left: 20px;
  padding-top: 10px;
  width: 100%;
  border-bottom: 2px solid #e0e0e0;
  box-sizing: border-box;
}

.toolbar-wrapper {
  background: #f6f6f6;
  width: 300px;
  border-left: 2px solid #e0e0e0;
  box-sizing: border-box;
}

/*












*/

.line {
  position: absolute;
  background: #5a5a5a;
  width: 0;
  height: 0;
}

.line-start {
  position: absolute;
  background: #5a5a5a;
}

.line-end {
  position: absolute;
  background: #5a5a5a;
}

.add-line-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  z-index: 100;
}

.bead-item {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  height: 20px;
  width: 20px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  background: rgb(254, 204, 177);
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(254, 204, 177, 1)),
    color-stop(50%, rgba(241, 116, 50, 1)),
    color-stop(51%, rgba(234, 85, 7, 1)),
    to(rgba(251, 149, 94, 1))
  );
  background: linear-gradient(
    to bottom,
    rgba(254, 204, 177, 1) 0%,
    rgba(241, 116, 50, 1) 50%,
    rgba(234, 85, 7, 1) 51%,
    rgba(251, 149, 94, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#feccb1', endColorstr='#fb955e',GradientType=0 );
}

.button {
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-size: 13px;
  color: #000;
}

.mouse-selection {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  z-index: 9;
}

.toolbar-wrapper .fieldset {
  border-top: 1px solid #dfdfdf;
  border-bottom: 1px solid #dfdfdf;
  padding-bottom: 5px;
  padding-top: 5px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.toolbar-wrapper .fieldset .label {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 30px;
  font-weight: 300;
  padding: 0;
  margin: 0;
  padding-left: 10px;
  padding-right: 10px;
}

.toolbar-wrapper .fieldset .input {
  border: none;
  background: none;
  border-bottom: 1px solid #dfdfdf;
  color: #6e6e6e;
  font-size: 14px;
  line-height: 30px;
  font-family: "Open Sans", sans-serif;
  box-shadow: none;
  max-width: 200px;
  padding: 0;
  margin: 0;
}
.toolbar-wrapper .fieldset .input[type="number"] {
  max-width: 50px;
}

